::ng-deep #QSIFeedbackButton-btn {
    top: 43%;
    transform: translate(0%, -45%);
    bottom: inherit !important;
    height: 200px !important;
    width: initial !important;
}

::ng-deep #QSIFeedbackButton-btn div {
    border-radius: 1px !important;
    transform: rotateZ(0) !important;
    text-transform: uppercase;
}

::ng-deep #QSIFeedbackButton-close-btn-container {
    display: none !important;
}

.sideBtn button {
    position: fixed;
    top: 70%;
    right: -63px;
    color: #fff;
    background: #1072ce;
    border-radius: 1px !important;
    border: none;
    font-size: 12px;
    width: 160px;
    z-index: 999;
    -webkit-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    -o-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
}

.app-content {
    position: fixed;
    top: 50%;
    -webkit-transform: translate(0, -50%);
    transform: translate(0, -50%);
    right: 0;
    font-size: 12px !important;
    width: 400px;
    background: #fff;
    padding: 0;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, .54);
    z-index: 999;
    height: 100%;
    overflow: auto;
}

.sideBtn.opened button {
    right: 337px !important;
    box-shadow: 5px 6px 17px 0 rgb(0 0 0 / 5%);
}

textarea {
    width: 100%;
    height: 130px;
}

#if {
    height: 60%;
}

::ng-deep .QSIFeedbackButton img {
    transform: rotate(180deg);
}

::ng-deep #QSIFeedbackButton-target-container {
    bottom: inherit !important;
    top: 50%;
    transform: translate(0%, -50%);
}

@media screen and (max-width: 767px) {
    ::ng-deep #QSIFeedbackButton-btn {
        display: none;
    }
}